import { ZIMLET_NAME, URL_PROPERTY_NAME } from '../constants';
import get from 'lodash/get';

let jitsiHostName = "meet.penso.com";

/**
 * generates a series of random strings (length 10) and joins them to create a random room id
 *
 * @param {Number} count	how many random strings to generate
 */
export function generateRoomId(count) {
	let id = '';

	for (let i = 0; i < count; i++) {
		const partialId = Math.random()
			.toString(36)
			.substring(3);

		if (i === 0) {
			id = partialId;
		} else {
			id += '-' + partialId;
		}
	}

	return id;
}

// generate the hostname of the Jitsi server, based on the current mail server's hostname
export function getJitsiHostname(zimlets = []) {
	if (jitsiHostName) return jitsiHostName;

	// find the jitsi zimlet from the config
	const jitsiZimlet = zimlets.find(zimlet => ZIMLET_NAME === get(zimlet, 'zimlet.0.name'));

	if (jitsiZimlet) {
		const globalConfig = get(jitsiZimlet, 'zimletConfig.0.global') || [];

		let jitsiUrl;

		// find the config with the jitsiUrl as name
		globalConfig.forEach(({ property }) => {
			property = property || [];
			property.forEach(({ name, content }) => {
				if (name === URL_PROPERTY_NAME) {
					jitsiUrl = content;
				}
			});
		});

		jitsiHostName = jitsiUrl;

		return jitsiUrl;
	}
}

function extractAddressFromRawLocation(loc) {
	const addr = typeof loc === 'string' ? loc.replace(/"/g, '').split(' ')[0] : loc.address;

	const jitsiHostUrl = getJitsiHostname();

	// we want to check against the specific server, not any random jitsi that
	// someone may have entered either manually or through the video zimlet
	if (addr.includes(jitsiHostUrl)) {
		return addr;
	}
}

export function extractJitsiJoinURL(locations = []) {
	const found = locations.map(extractAddressFromRawLocation).filter(Boolean);

	return found.length ? found[0] : '';
}

export function buildLocationFromJitsiJoinURL(url) {
	return { name: url, address: url, zimbraCalResType: 'Location' };
}

// zm-x-web/src/components/calendar/appointment-edit/index.js is where
// handleLocationChange is defined. Near the end, some code is annotated as
// ordering locations "such that all user text inputs come at the end", but it
// really just saves the last one. Anyway, that's why we can't just split() on
// a semicolon. Because we're trying really hard to make the Jitsi location look
// like and be presented as a hard contact when it's just an address.
export function rebuildLocationsWithJitsiJoinURL(locations = [], replaceURL = '') {
	const rebuilt = [];

	for (const locEntry of locations) {
		let splits;

		if (typeof locEntry === 'string') {
			const ii = locEntry.indexOf(';');
			splits =
				ii >= 0 && ii !== locEntry.length - 1
					? [locEntry.substring(0, ii), locEntry.substring(ii + 1).trimLeft()]
					: [locEntry];
		} else {
			splits = [locEntry];
		}

		for (let loc of splits) {
			const url = extractJitsiJoinURL([loc]);
			if (url) {
				// If passed a replaceURL, then kill any existing Jitsi URL in
				// locations.
				loc = replaceURL ? '' : buildLocationFromJitsiJoinURL(url);
			}

			if (loc) {
				rebuilt.push(loc);
			}
		}
	}

	if (replaceURL) {
		rebuilt.push(buildLocationFromJitsiJoinURL(replaceURL));
	}

	return rebuilt;
}
